.Btn_sec{
    padding-right: 29px !important;
    padding-bottom: 22px !important;
}
.submit_btn{
    background-color: #007aff;
    border:1px solid #007aff ;
    font-size: 2vh;
    padding: 0.5vh 1.5vh;
    border-radius: 5px;
    color: white;
    transition-duration:0.4s ;
    cursor: pointer;
}
.submit_btn:hover{
    color:#007aff;
    border:1px solid #007aff ;
    background-color: white;
}
.cancel_btn{
    background-color: white;
    font-size: 2vh;
    color: #df2447;
    border:1px solid white;
    cursor: pointer;
    padding: 0.5vh 1.5vh;
    transition-duration:0.4s ;
}
.cancel_btn:hover{
   color:white;
   border:1px solid #df2447;
   background-color: #e48395b5;
   border-radius: 5px;
}
